import axios from "axios";
import host from "../../../config/host";
const state = () => ({
  dataTodo: "",
  editData: "",
  textSearch: "",
  error: ""
});
const getters = {};
const mutations = {
  TODO_EDIT(state, payload) {
    state.editData = payload;
  },
  TEXT_SEARCH(state, payload) {
    state.textSearch = payload;
  },
  GET_DATA_TODO(state) {
    axios({
      method: "GET",
      url: host + "/api/to-do-list?search=" + state.textSearch,
      headers: {
        Authorization: `Bearer token=${$cookies.get("token")}`,
        "Content-Type": "application/json"
      }
    })
      .then(res => {
        if (res.data.code == 200) {
          state.dataTodo = res.data.data;
          localStorage.setItem("dataTodo", JSON.stringify(res.data.data));
          return;
        }
        if (res.data.code == 404) {
          state.error = res.data.message;
        }
      })
      .catch(err => {
        state.error = err;
      });
  },
  ADD_DATA_TODO(state, payload) {
    axios({
      method: "POST",
      url: host + "/api/to-do-list",
      data: { title: payload.title, content: payload.content },
      headers: {
        Authorization: `Bearer token=${$cookies.get("token")}`,
        "Content-Type": "application/json"
      }
    })
      .then(res => {
        if (res.data.code == 200) {
          state.dataTodo.push(res.data.data);
          return;
        }
        if (res.data.code == 404) {
          state.error = res.data.message;
        }
      })
      .catch(err => (state.error = err));
  },
  EDIT_DATA_TODO(state, payload) {
    axios({
      method: "PUT",
      url: host + "/api/to-do-list?id=" + payload._id,
      data: { title: payload.title, content: payload.content },
      headers: {
        Authorization: `Bearer token=${$cookies.get("token")}`,
        "Content-Type": "application/json"
      }
    })
      .then(res => {
        if (res.data.code == 200) {
          let arr = state.dataTodo.map(value => {
            if (value._id == payload._id) {
              return {
                ...value,
                title: payload.title,
                content: payload.content
              };
            }
            return value;
          });
          return (state.dataTodo = arr);
        }
        if (res.data.code == 404) {
          state.error = res.data.message;
        }
      })
      .catch(err => {
        state.error = err;
      });
  },
  DELETE_DATA_TODO(state, _id) {
    axios({
      method: "DELETE",
      url: host + "/api/to-do-list?id=" + _id,
      headers: {
        Authorization: `Bearer token=${$cookies.get("token")}`,
        "Content-Type": "application/json"
      }
    })
      .then(res => {
        if (res.data.code == 200) {
          let data = state.dataTodo.filter((value, index) => {
            if (value._id != _id) {
              return value;
            }
          });
          return (state.dataTodo = data);
        }
        if (res.data.code == 404) {
          state.error = res.data.message;
        }
      })
      .catch(err => {
        state.error = err;
      });
  }
};
const actions = {
  TODO_EDIT(context, payload) {
    context.commit("TODO_EDIT", payload);
  },
  TEXT_SEARCH(context, payload) {
    context.commit("TEXT_SEARCH", payload);
  },
  GET_DATA_TODO(context) {
    context.commit("GET_DATA_TODO");
  },
  ADD_DATA_TODO(context, payload) {
    context.commit("ADD_DATA_TODO", payload);
  },
  EDIT_DATA_TODO(context, payload) {
    context.commit("EDIT_DATA_TODO", payload);
  },
  DELETE_DATA_TODO(context, _id) {
    context.commit("DELETE_DATA_TODO", _id);
  }
};
export default {
  state,
  getters,
  mutations,
  actions
};



