<template>
  <div class>
    <form class="bg-light" onsubmit="{this.handleSubmit}">
      <div class="form-group">
        <label for="title" class="text-light">Title</label>

        <!--use v-if Check status to handle tag input -->
        <input
          type="text"
          class="form-control"
          id="title"
          aria-describedby="emailHelp"
          placeholder="Title"
          v-model="title"
        />
      </div>
      <div class="form-group">
        <label for="content" class="text-light">Content</label>
        <input
          type="text"
          class="form-control"
          id="content"
          placeholder="Content"
          v-model="content"
        />
        <!-- ////// -->
      </div>
      <div class="d-flex justify-content-end align-items-end py-2">
        <div class="px-2">
          <button type="button" class="btn text-light btn-secondary" @click="statusEditForm">Cancel</button>
        </div>
        <div>
          <button type="button" class="btn btn-primary" @click="handlebBtnSave()">Save</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "axios";
import host from "../../../../config/host";
export default {
  props: ["statusEditForm"],
  data() {
    return {
      _id: "",
      title: this.$store.state.todo.editData.title,
      content: this.$store.state.todo.editData.content
    };
  },
  mounted() {
    this._id = this.$store.state.todo.editData._id;
  },
  methods: {
    handlebBtnSave() {
      let payload = { _id: this._id, title: this.title, content: this.content };
      this.$store.dispatch("EDIT_DATA_TODO", payload);
      this.statusEditForm();
    }
  }
};
</script>

<style scoped>
</style>


