<template>
  <div class="container">
    <div class="py-3 text-danger">{{$store.state.todo.error}}</div>
    <SearchTodo />
    <div class="addNew" @click="statusAddForm">
      <div class="modal-add" id="modal-add">
        <img class="icon" src="../../../assets/Apps-Dialog-Add-icon.png" alt />
      </div>
      <label for="modal-add">Add New</label>
    </div>
    <AddForm v-if="statusForm=='add'" :statusAddForm="statusAddForm" />
    <EditForm v-if="statusForm=='edit'" :statusEditForm="statusEditForm"  />
    <Table :statusEditForm="statusEditForm" />
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import SearchTodo from "./SearchTodo";
import Table from "./table/Table";
import AddForm from "./AddForm";
import EditForm from "./EditForm";
import axios from "axios";
import host from "../../../../config/host";
export default {
  components: {
    SearchTodo,
    Table,
    AddForm,
    EditForm
  },
  data() {
    return {
      data: "",
      statusForm: "hide"
    };
  },
  mounted() {
    this.$store.dispatch("GET_DATA_TODO");
  },
  computed: {},
  methods: {
    statusAddForm() {
      if (this.statusForm == "add") {
        return (this.statusForm = "hide");
      }
      return (this.statusForm = "add");
    },
    statusEditForm() {
      if (this.statusForm == "edit") {
        return (this.statusForm = "hide");
      }
      return (this.statusForm = "edit");
    },
  }
};
</script>

<style lang="scss" scoped>
.addNew {
  display: flex;
  justify-content: start;
  align-items: center;
  background: gray;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  cursor: pointer;
}
label {
  font-size: 2rem;
  padding-left: 1rem;
  cursor: pointer;
}
.icon {
  height: 40px;
  width: 40px;
  margin-left: 0.5rem;
}
</style>



