<template>
  <div>
    <div>_id: {{$route.params._id}}</div>
    <div>{{findItem($route.params._id)}}</div>
  </div>
</template>

<script>
export default {
  methods: {
    findItem(_id) {

      return this.$store.state.todo.dataTodo.find(item => item._id == _id);
    },
  }
};
</script>

<style scoped>
</style>


